/* Using box model */
* {
  box-sizing: border-box;
}

/* Maps heatmap */
.heatmap {
  width: 100%;
  margin: 0 auto;
  font-family: Arial, sans-serif;
  text-align: center;

  &__block {
    &__info {
      display: inline-block;
      vertical-align: top;
      width: 450px;
      text-align: left;

      &__item {
        margin-bottom: 20px;
      }
    }

    &__map {
      display: inline-block;
      width: 100%;

      &--australia {
        .svg-map {
          stroke-width: 0.5; // Australia stroke is larger than other maps
        }
      }

      &--sa {
        width: 100%; //  needs more space	for tooltip

        .svg-map__location {
          &--heat0 {
            fill: #f5f5f5;
          }

          &--heat1 {
            fill: #ffdab9;
          }

          &--heat2 {
            fill: #ffb18e;
          }

          &--heat3 {
            fill: #ff6347;
          }

          &:focus,
          &:hover {
            opacity: 0.75;
          }
        }
      }

      &__tooltip {
        font-family: "Cairo", sans-serif;
        position: fixed;
        width: 200px;
        padding: 10px;
        border: 1px solid darkgray;
        background-color: white;
      }
    }
  }
}
